// 这是文章列表的每一条的结构加数据
<template>
  <div>
    <!-- 每一条资讯项的结构 -->
    <van-cell>
      <!-- 标题区域的插槽 -->
      <template #title>
        <div class="title-box">
          <!-- 标题 -->
          <span>{{ articleObj.title }}</span>
          <!-- 单图情况 -->
          <!-- 利用图片占位图，如果发生错误，就报加载失败 -->
          <!-- <img :src="articleObj.cover.images[0]" alt="" class="thumb" v-if="articleObj.cover.type === 1" /> -->
          <van-image v-if="articleObj.cover.type === 1" :src="articleObj.cover.images[0]" class="thumb">
            <template v-slot:error>图片不在辣</template>
          </van-image>
        </div>
        <!-- 三图情况 -->
        <div class="thumb-box" v-if="articleObj.cover.type === 3">
          <!-- <img :src="item" alt="" v-for="(item, i) in articleObj.cover.images" :key="i" class="thumb" /> -->
          <van-image :src="item" alt="" v-for="(item, i) in articleObj.cover.images" :key="i" class="thumb">
            <template v-slot:error>图片不在辣</template>
          </van-image>
        </div>
      </template>

      <!-- label 区域的插槽 -->
      <template #label>
        <div class="label-box">
          <div>
            <span>{{ articleObj.aut_id }}</span>
            <span>{{ articleObj.comm_count }}评论</span>
            <span>{{ timeAgo(articleObj.pubdate) }}</span>
          </div>
          <!-- 反馈按钮 -->
          <!-- 需求：希望在搜索结果页面不显示反馈按钮 -->
          <!-- 解决：首先给反馈按钮标签加一个v-if,再在props定义这个值，最后由使用者传参进来控制即可 -->
          <van-icon name="cross" @click.stop="show = true" v-if="isShow" />
        </div>
      </template>
    </van-cell>
    <!-- 反馈面板 -->
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" @cancel="cancel" :cancel-text="cancelText" get-container="body" />
  </div>
</template>

<script>
// 导入的方法不能直接使用，要去export default{}中注册之后才能使用
import { timeAgo } from '@/utils/date.js'
import { firstActions, secondActions } from '@/api/feedback.js'
export default {
  data() {
    return {
      // 控制反馈组件的显示于隐藏
      show: false,
      // 配置反馈组件的选项
      actions: firstActions,
      // 控制反馈面板的返回与取消
      cancelText: '取消'
    }
  },
  props: {
    // 每一个文章对象
    articleObj: Object,
    isShow: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    timeAgo,
    // 点击反馈组件时的函数
    onSelect(action) {
      if (action.name === '反馈垃圾内容') {
        this.actions = secondActions
        this.cancelText = '返回'
        // 默认情况下点击选项时不会自动收起
        // 可以通过 close-on-click-action 属性开启自动收起
        // this.show = false
      } else if (action.name === '不感兴趣') {
        // z子向父传值用自定义事件
        this.$emit('disLikeEV', this.articleObj.art_id)
        this.show = false // 无论成功/失败-直接让反馈面板隐藏
      } else {
        // 点击二级面板
        this.$emit('feedbackDustbin', this.articleObj.art_id, this.actions.value)
        this.show = false
      }
    },
    // 控制反馈面板的返回与取消
    // 之所以要用cancel方法，是因为onSelect它不能控制底部按钮
    cancel() {
      if (this.cancelText === '返回') {
        this.actions = firstActions
        this.cancelText = '取消'
        this.show = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
/* 标题样式 */
.title-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

/* label描述样式 */
.label-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 文章信息span */
.label-box span {
  margin: 0 3px;
  &:first-child {
    margin-left: 0;
  }
}

/* 图片的样式, 矩形黄金比例：0.618 */
// 单图
.thumb {
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}

/* 三图, 图片容器 */
.thumb-box {
  display: flex;
  justify-content: space-between;
}
</style>
